import { Component, Prop, Vue } from "vue-property-decorator";

import { auth, firestoreDb } from "@/firebaseInit";
import { CartOrder } from "@/dto/CartOrder";
import { LibraryItem } from "@/dto/LibraryItem";
import { query, collection, orderBy, limit, getDocs, where } from "firebase/firestore";
import { TicketInfo } from "@/dto/TicketInfo";

@Component
export default class UserTickets extends Vue {

  libraryList: TicketInfo[] = [];
  selectedTicketItem: TicketInfo = new TicketInfo();


  loading = false;
  infoLoading = false;
  noOrders = false;
  headers: any = [
    { text: '', value: 'headerImageUrl', sortable: false },

    { text: 'DATE', value: 'createdDate', },
    { text: 'TICKET ID', value: 'ticketId' },
    { text: 'TICKET TYPE', value: 'ticketType' },
    { text: 'TICKET STATUS', value: 'ticketStatus' },
    { text: '', value: 'actions', sortable: false },
  ]
  redeemModal = false;
  async mounted() {
    await this.loadTickets();

  }


  async loadTickets() {

    try {
      this.infoLoading = true;
      const userDetails = auth.currentUser;
      if (userDetails) {
        const q = query(collection(firestoreDb, `tickets`).withConverter<TicketInfo>(TicketInfo.converter), where("uid", "==", userDetails.uid), orderBy("createdDate", "desc"), limit(20));
        const resp = await getDocs(q);
        if (!resp.empty) {
          resp.forEach((doc: any) => {
            this.libraryList.push(doc.data());
          });
        } else {
          this.noOrders = true;
        }
      } else {
        Vue.toasted.show('User not logged in', { icon: "close-circle", type: "error" });
      }
    } catch (error) {
      console.log(error);
      Vue.toasted.show(error, { icon: "close-circle", type: "error" });

    }
    finally {
      this.infoLoading = false;
    }
  }
  async processKey(order: TicketInfo) {
    try {

      this.redeemModal = true;
      this.selectedTicketItem = order;

    } catch (error) {
      console.log(error);
    }
  }


}
